.content-block{
  background-color: var(--contentblock-background);
 /* background-image: linear-gradient(var(--contentblock-background),var(--contentblock-fade) 700%)
   */  
   max-width:100rem; 

  background-image: linear-gradient(90deg, transparent 35%, var(--contentblock-fade) 100%);
  background-size: 0.3rem;
}

.text{
  text-align:left;
}


@media(min-width:1250px){
    .content-block {
      height:40rem;
      width:90%;
  
      z-index: 10;
      
      display: grid;
      
      /*box-shadow: 0 2px 2px rgba(0, 0, 0, 0.13);
      */box-shadow: 5px 5px 10px var(--shadows),5px 5px var(--shadows);
      border: 0.3rem solid var(--contentBorder);
      border-radius: 2rem;
    }



    /* Initial state */
.content-block {
  grid-template-columns: 20rem 1fr;
}

/* Animation for enlarging image */
.content-block.image-enlarge {
  animation: imageEnlarge 0.1s forwards;
}


/* Animation for reverting to default size */
.content-block.image-normal {
  animation: imageToDefault 0.5s forwards;
}


/* Keyframes for enlarging image */
@keyframes imageEnlarge {
  from {
    grid-template-columns: 20rem 1fr;
    font-size:1.4rem;
  }
  to {
    grid-template-columns: 30rem 1fr;
    font-size:1.3rem;
  }
}

/* Keyframes for reverting to default size */
@keyframes imageToDefault {
  0% {
    grid-template-columns: 30rem 1fr;
    font-size:1.3rem;
  }
  30%{
    grid-template-columns: 30rem 1fr;
    font-size:1.3rem;
  }
  100% {
    grid-template-columns: 20rem 1fr;
    font-size:1.4rem;
  }
}


    .text{
      width:80%;
      margin:3%;
      margin-left: 10%;
    }
    
  }
  

  .text h2{
    font-size: 200%;
  }
  .text p{
    font-size: 120%;
  }

  
  
    @media(max-width:1250px){

      .content-block{
        bottom:-10vh;
        height:70vh;
        padding:2rem;
        padding-top: 0;
        padding-bottom: 0;
        border-radius: 2rem;
        margin: 5vw;
        margin-top: 0;
        width:100vw;
  
        position: relative;
        
        z-index: 10;
        display: grid;
        grid-template-rows: 20rem 1fr;
        scroll-margin-left: 20rem;
        scroll-margin-right: 20rem;
  
        box-shadow: 0 2px 2px rgba(0, 0, 0, 0.13);
        border: 0.3rem solid var(--contentBorder);
        scroll-snap-align: start;
  
      }

      .text{
        position: relative;
        height: 100%;
        top:2vh;
        width:90%;
        transform: translateX(-5%);
        padding:5vh;
        padding-top: 0;
        text-align:left;

      }

      @media(max-height:1150px){
      .text{
        position: absolute;
        left:5vw;
        height: 100%;
        top:25vh;
        width:65vw;
        transform: translateX(-10%);
        padding:5vh;

      }
      .text h2{
        font-size: 2vh;
      }
      .text p{
        font-size: 1.8vh;
      }
    
    }

  
  
    }
  
    .text h2{
      letter-spacing: -0.1rem;
      color:var(--projectSubject);
    }
    .text p{
      letter-spacing: 0.05rem;
      color:var(--projectText);
    }


    @media(max-width:620px){
      .text{
        color:rgba(255, 255, 255, 0);
      }
  
  
    }

    
  
  
  
    





