.image{
  position: relative;
  overflow: hidden;
}

@media(min-width:1250px){

 .image{
    height:40rem;

    border-radius: 20px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.13);
  }
}

@media(max-width:1250px){

  .image{
    height: 30vh;
    width:65vw;
    object-fit: cover;
    border-radius: 1rem;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.13);
  } 
}

.hide-button{
  width:0px;
  height:0px;
}

.carousel-button {
  position: absolute;
  width: 1rem;
  top: 0;

  z-index: 1000;
  border: none;
  background-color: var(--carouselButtons);
  /*mix-blend-mode: difference; /* Apply the inversion effect */
  opacity: 0.7;
  box-shadow: 0 4px 14px rgba(0, 0, 0, 0.479);
}
.carousel-button:hover {
  opacity: 0.6;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.479);

}

.carousel-button.previous {
  left: 0.2rem;
  top:30%;
  bottom:30%;
  border-top-left-radius: 1rem;
  border-bottom-left-radius: 1rem;
}
.carousel-button.next {
  right: 0.2rem;
  top:30%;
  bottom:30%;
  border-top-right-radius: 1rem;
  border-bottom-right-radius: 1rem;
}

.carousel-image{
  position: absolute;
}
