body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Quicksand';

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
  height: 100vh; /* Set the body height to 100% of the viewport height */
  /*overflow-x: hidden; /* Prevent scrollbars from appearing */
}

h1, h2, h3 {
  font-family: 'Nunito', sans-serif;
}


p{
  font-family:'Quicksand';
  font-size:1.4rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  /*color pallet*
  --lightCream:#faf4ea8c;
  --coral:#fac461;*/

  --lightCream:#3f1b0a23;
  --coral:#ff9767;
  --sandBlue1:#d8f1ff7e;
  --sandBlue2:#88d3ff;
  --sandBlue3:#62c5ff;
  --sandBlue4:#1fadff;
  --sandBlue5:#062333;
  --sandBlue6:#06233334;
  --corals:#e98b5f6b;
  --darkGrey:#3f1b0a77;
  --darkGreyer:#000000;
  --white:#FFFFFF;
  --transparant:#FFFFFF00;
  
  /*
  --lightCream:#FFF6DC;
  --coral:#ffc6ac;
   --darkGrey:#202228;
  */
  
  --darkCoral:#9c502d;
  --lime:#32CD32;
  --blue:#284B63;
  --grey:#808080;
  --gold:#FFD700;
  --lightPeach:#FFC6AC;
  --coral2:#FF7F50 ;
  --aquamarine:#ABFFE4;
}

:root{
  --main-background:var(--sandBlue3);
  --accent-background:var(--sandBlue1);
  --text:var(--blue);
  --landingpage-title:var(--sandBlue5);
  --landingpage-nameplate:var(--transparant);
  --projectTitle:var(--lightCream);
  --projectSubject:var(--darkGrey);
  --projectText:var(--darkGreyer);
  --contentblock-background:var(--sandBlue2);
  --contentblock-fade:var(--sandBlue2);
  --contentBorder:var(--sandBlue2);
  --navbarBackground:var(--lightCream);
  --shadows:var(--sandBlue6);
  --carouselButtons:var(--white);
}
